:root {
  --color-primary: #ff4895;
  --color-foreground: #121212;
  --color-background: #fff;
}

@media (prefers-color-scheme: dark) {
  :root {
    --color-primary: #ff4895;
    --color-foreground: #fff;
    --color-background: #121212;
  }
}

$font-family_sans: "ARS Maquette", ars-maquette-web, "Avenir Next", Avenir, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
$font-family_serif: leitura-news, Georgia, "Times New Roman", serif;

html {
	font-family: $font-family_sans;
	line-height: 1.6;
	color: var(--color-foreground);
  background-color: var(--color-background);
  overflow: none;
  cursor: crosshair;
  @media only screen and (min-width: 800px) {
  }
  @media only screen and (max-width: 800px) {
    padding: 50px;
  }
  @media only screen and (max-width: 500px) {
    padding: 10px;
  }
}

body {
  display: flex;
  flex-direction: column;
  margin-left: auto;
  margin-right: auto;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

h1, ul {
  width: 80%;
}

h1 {
  font-weight: 700;
  font-size: 5.25rem;
  font-family: $font-family_sans;
  margin: 0 0 1rem 0;
  line-height: 0.8;
  text-transform: uppercase;
  letter-spacing: -2px;
  @media only screen and (max-width: 800px) {
    font-size: 3.5rem;
  }
  @media only screen and (max-width: 666px) {
    font-size: 2.75rem;
  }
  @media only screen and (max-width: 400px) {
    font-size: 2.25rem;
  }
}

ul {
  margin: 0 0 1rem 0;
  padding-left: 0;
}

li {
  list-style: none;
}

s {
  position: relative;
  text-decoration: none;
  &:after {
    position: absolute;
    left: -2px;
    top: 52%;
    width: 108%;
    height: 3px;
    content: "";
    transform: rotate(-1deg);
    background: var(--color-primary);
  }
}

a {
  padding: 3px 4px 3px 7px;
  font-size: 0.75rem;
  letter-spacing: 0.15rem;
  text-transform: uppercase;
  text-decoration: none;
  color: inherit;
  background-color: rgba(255, 255, 255, 0.1);
}

a:focus,
a:hover {
  color: var(--color-primary);
  background-color: rgba(255, 255, 255, 0.05);
}
